import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import NavBar from '../components/Navbar'
import { useNavigate } from 'react-router-dom'
import "./Signup.css"

const Signup = () => {

    const navigate=useNavigate()
    const [credentials, setCredentials] = useState({ name: "", email: "", password: "", geolocation: "" })

    const handleSubmit = async (e) => {
        e.preventDefault()
        const response = await fetch("http://localhost:5000/api/createuser", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ name: credentials.name, email: credentials.email, password: credentials.password, location: credentials.geolocation })
        })
        const json = await response.json()
        console.log(json)

        if (json.success) {
            //save the auth toke to local storage and redirect
            localStorage.setItem('token', json.authToken)
            navigate("/login")
      
          }
          else {
            alert("Enter Valid Credentials")
          }

    }

    const onChange = (e) => {
        setCredentials({ ...credentials, [e.target.name]: e.target.value })
    }

    return (
        <>
        <div className='sigone'>
            <NavBar/>
            <div className="sigtwo container">
                <form className='sigtwoone w-50 m-auto mt-5 border bg-dark border-success rounded' onSubmit={handleSubmit} >
                    <div className="form-group m-3">
                        <label htmlFor="name" className='form-label'>Name</label>
                        <input type="text" className="form-control" placeholder="Enter name" name='name' value={credentials.name} onChange={onChange} />
                    </div>
                    <div className="form-group m-3">
                        <label htmlFor="email">Email address</label>
                        <input type="email" className="form-control" placeholder="Enter email" name='email' value={credentials.email} onChange={onChange} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="location">Password</label>
                        <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" name='password' value={credentials.password} onChange={onChange} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="location">Address</label>
                        <input type="text" className="form-control" id="exampleInputPassword1" placeholder="Location" name='geolocation' value={credentials.geolocation} onChange={onChange} />
                    </div>
                    <button type="submit" className="btn btn-success">Submit</button>
                    <Link to="/login" className='m-3 btn btn-danger' >Already a user</Link>
                </form>
            </div>
            </div>
        </>
    )
}

export default Signup
