import React, { useEffect, useState } from "react";
import NavBar from "../components/Navbar";
import Footer from "../components/Footer";
import Card from "../components/Card";
import Carousel from "react-bootstrap/Carousel";
import "./Home.css";

const Home = () => {
  const [search, setSearch] = useState("");
  const [foodCat, setFoodCat] = useState([]);
  const [foodItem, setFoodItem] = useState([]);

  const loadData = async () => {
    let response = await fetch("https://12bajekatry.vercel.app", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
    });
    response = await response.json();

    setFoodItem(response[0]);
    setFoodCat(response[1]);
  };

  useEffect(() => {
    loadData();
  }, []);

  return (
    <div className="one">
      <div>
        <NavBar />
      </div>
      <div>
        <div
          className="carouselExample"
          style={{ objectFit: "contain !important" }}
        >
          <div className="carousel" id="carousel">
            <Carousel>
              <Carousel.Item>
                {/* <Carousel.Caption style={{ zIndex: "10" }}> */}
                {/* <form className="form-inline d-flex justify-content-center "> */}
                {/* <input */}
                {/* className="form-control mr-sm-2" */}
                {/* type="search" */}
                {/* placeholder="Search" */}
                {/* aria-label="Search" */}
                {/* value={search} */}
                {/* onChange={(e) => { */}
                {/* setSearch(e.target.value); */}
                {/* }} */}
                {/* /> */}
                {/* <button className="btn btn-outline-success text-white bg-success my-2 my-sm-0" type="submit">Search</button> */}
                {/* </form> */}
                {/* </Carousel.Caption> */}
                <img
                  className=" firstimage d-block w-100 cursor-pointer"
                  src="https://source.unsplash.com/random/900x700?burger"
                  alt="First slide  "
                  style={{ filter: "brightness(30%" }}
                />
              </Carousel.Item>
              <Carousel.Item>
                <img
                  className="firstimage d-block w-100"
                  src="https://source.unsplash.com/random/900x700?momo"
                  alt="Second slide"
                  style={{ filter: "brightness(30%" }}
                />
                {/* <Carousel.Caption style={{ zIndex: "10" }}> */}
                {/* <form className="form-inline"> */}
                {/* <input */}
                {/* // className="form-control mr-sm-2" */}
                {/* // type="search" */}
                {/* // placeholder="Search" */}
                {/* // aria-label="Search" */}
                {/* // value={search} */}
                {/* // onChange={(e) => { */}
                {/* // setSearch(e.target.value); */}
                {/* // }} */}
                {/* // /> */}
                {/* <button className="btn btn-outline-success text-white bg-success my-2 my-sm-0" type="submit">Search</button> */}
                {/* </form> */}
                {/* </Carousel.Caption> */}
              </Carousel.Item>
              <Carousel.Item>
                <img
                  className="firstimage d-block w-100"
                  src="https://source.unsplash.com/random/900x700?pizza"
                  alt="Third slide"
                  style={{ filter: "brightness(30%" }}
                />

                {/* <Carousel.Caption style={{ zIndex: "10" }}> */}
                {/* <form className="form-inline"> */}
                {/* <input */}
                {/* className="form-control mr-sm-2" */}
                {/* type="search" */}
                {/* placeholder="Search" */}
                {/* aria-label="Search" */}
                {/* value={search} */}
                {/* onChange={(e) => { */}
                {/* setSearch(e.target.value); */}
                {/* }} */}
                {/* /> */}
                {/* <button className="btn btn-outline-success text-white bg-success my-2 my-sm-0" type="submit">Search</button> */}
                {/* </form> */}
                {/* </Carousel.Caption> */}
              </Carousel.Item>
            </Carousel>
          </div>
        </div>{" "}
      </div>
      <form className="searchbar form-inline">
        <input
          className="form-control mr-sm-2"
          type="search"
          placeholder="Search"
          aria-label="Search"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
          }}
        />
        {/* <button className="btn btn-outline-success text-white bg-success my-2 my-sm-0" type="submit">Search</button> */}
      </form>
      <div className="container">
        {foodCat !== []
          ? foodCat.map((data) => {
              return (
                <div className="row mb-3">
                  <div key={data._id} className="fs-3 m-3 text-white mt-4 mb-1">
                    {data.CategoryName}
                  </div>
                  <hr />
                  {foodItem !== [] ? (
                    foodItem
                      .filter(
                        (item) =>
                          item.CategoryName === data.CategoryName &&
                          item.name
                            .toLowerCase()
                            .includes(search.toLocaleLowerCase())
                      )
                      .map((filterItems) => {
                        return (
                          <div
                            className="cardborder col-12 col-md-6 col-lg-3"
                            key={filterItems._id}
                          >
                            <Card
                              foodItem={filterItems}
                              options={filterItems.options[0]}
                            ></Card>
                          </div>
                        );
                      })
                  ) : (
                    <div>No Such Data Found</div>
                  )}
                </div>
              );
            })
          : ""}
      </div>
      <div>
        <Footer />
      </div>
    </div>
  );
};

export default Home;
