import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import NavBar from '../components/Navbar'
import "./Login.css"

const Login = () => {
  let navigate = useNavigate()
  const [credentials, setCredentials] = useState({ email: "", password: "" })

  const handleSubmit = async (e) => {
    e.preventDefault()
    const response = await fetch("http://localhost:5000/api/loginuser", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ email: credentials.email, password: credentials.password })
    })
    const json = await response.json()
    console.log(json)

    if (!json.success) {
      alert("Enter Valid Credentials")
    }
    if (json.success) {
      localStorage.setItem("userEmail", credentials.email)
      localStorage.setItem("authToken", json.authToken)
      navigate("/")
    }

  }

  const onChange = (e) => {
    setCredentials({ ...credentials, [e.target.name]: e.target.value })
  }

  return (
    <>
      <div className='logone'>

      <NavBar  />
      <div className="container logsecond">
        <form className='logo2one w-50 m-auto mt-5  ' onSubmit={handleSubmit} >
          <div className="logthrird form-group">
            <label htmlFor="email">EMAIL ADDRESS</label>
            <input type="email" className="form-control" placeholder="Enter email" name='email' value={credentials.email} onChange={onChange} />
            <hr />
            {/* <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small> */}
          </div>
          <div className="form-group">
            <label htmlFor="location">Password</label>
            <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" name='password' value={credentials.password} onChange={onChange} />
          </div>
          <button type="submit" className=" formbuttin">Submit</button>
          <Link to="/createuser" className='m-3 btn btn-primary rounded ' >I'm a new user</Link>
        </form>
      </div>
      </div>
    </>
  )
}

export default Login
