import React from "react";
import { Link, useNavigate } from "react-router-dom";
import Badge from "react-bootstrap/Badge";
import "./Navbar.css";
import { useState } from "react";
import Modal from "../Modal";
import Cart from "../screens/Cart";
import { useCart } from "./ContextReducer";
import "./Navbar.css";
import image from "../images/logofoodify1.jpeg";
import {MdYoutubeSearchedFor} from "react-icons/md"

const NavBar = () => {
  const [search, setSearch] = useState("");
  const [cartView, setCartView] = useState(false);
  let data = useCart();
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.removeItem("authToken");
    navigate("/login");
  };

  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark mainnav">
        <Link className="navbar-brand fs-1 fst-italic " to="/">
          <img src={image} alt="" />
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto me-auto mb-2 ">
            <li className="nav-item active">
              <Link className="nav-link active fs-5 cartdesign " to="/">
                Home<span className="sr-only"></span>
              </Link>
            </li>
            {localStorage.getItem("authToken") ? (
              <li className="nav-item active">
                <Link
                  className="nav-link active fs-5 cartdesign "
                  to="/myOrder"
                >
                  My Orders <span className="sr-only"></span>
                </Link>
              </li>
            ) : (
              ""
            )}
          </ul>
          {/* <form className="form-inline formi" >
            <MdYoutubeSearchedFor className="searchicon"/>
            <input
              className="form-control searchbuttin"
              type="search"
              placeholder="Search"
              aria-label="Search"
              value={search}
              onChange={(e)=>{
                setSearch(e.target.value)
              }}
              />
          </form> */}
          {!localStorage.getItem("authToken") ? (
            <div className="d-flex">
              <Link
                className="btn bg-dark text-danger mx-1 cartdesign"
                to="/login"
              >
                Login
              </Link>
              <Link
                className="btn bg-white text-danger mx-1 cartdesign "
                to="/createuser"
              >
                Signup
              </Link>
            </div>
          ) : (
            <div className="cartlogout">
              <div
                className="btn bg-white mx-2"
                onClick={() => {
                  setCartView(true);
                }}
              >
                MY Cart{" "}
                {
                  <Badge pill bg="dark">
                    {data.length}
                  </Badge>
                }
              </div>
              {cartView ? (
                <Modal onClose={() => setCartView(false)}>
                  <Cart />
                </Modal>
              ) : null}
              <div className="btn bg-white mx-2 " onClick={handleLogout}>
                Logout
              </div>
            </div>
          )}

          <form className="form-inline my-2 my-lg-0"></form>
        </div>
      </nav>
    </div>
  );
};

export default NavBar;
